* {
  box-sizing: border-box;
}

h1, h2, h3, p {
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

ul {
  list-style: none;
}

#search-box {
  font: inherit;
  max-width: 320px;
  color: #fff;
  background-color: #181818;
  border: 4px solid #4c49ea;
  border-radius: 16px;
  outline: rgba(0, 0, 0, 0);
  margin: 32px auto;
  padding: 8px;
  font-size: 20px;
}

.country-info-img {
  width: 48px;
  height: auto;
}

.country-list {
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  display: flex;
}

.country-list-img {
  width: 38px;
  height: auto;
}

.country-info {
  margin: 12px;
  display: inline-flex;
}

.ag-format-container {
  width: 1142px;
  margin: 0 auto;
}

.country-link, .country-list-item {
  background-color: #181818;
  border-radius: 28px;
  padding: 30px;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.country-link {
  margin: 0 auto;
}

.country-list-item {
  margin-right: auto;
  display: inline-flex;
}

.country-link:hover, .country-link:hover .country-subtitle {
  color: #fff;
}

.country-link:hover .country-bg {
  transform: scale(12.1);
}

.country-title {
  min-height: 87px;
  color: #fff;
  z-index: 2;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  position: relative;
  overflow: hidden;
}

.country-list-title {
  padding: 22px;
  font-size: 18px;
  display: inline-flex;
}

.country-subtitle-box {
  color: #fff;
  z-index: 2;
  margin-bottom: 8px;
  font-size: 18px;
  position: relative;
}

.country-subtitle-box:last-child {
  margin-bottom: 0;
}

.country-subtitle {
  color: #f9b234;
  -o-transition: color .5s cubic-bezier(.45, .05, .55, .95);
  font-weight: bold;
  transition: color .5s cubic-bezier(.45, .05, .55, .95);
}

.country-bg {
  height: 128px;
  width: 128px;
  z-index: 1;
  -o-transition: all .5s cubic-bezier(.45, .05, .55, .95);
  background-color: #4c49ea;
  border-radius: 50%;
  transition: all .5s cubic-bezier(.45, .05, .55, .95);
  position: absolute;
  top: -75px;
  right: -75px;
}

